import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      isLogin: false,
      userName: "",
      user_avatar: "",
      shopType: "",
      carrier: "",
      carrier_state: false,
      member_qrCode: "test_qrCode",
    };
  },
  mutations: {
    setLoginState(state) {
      state.isLogin = true;
    },
    setUserName(state, name) {
      state.userName = name;
    },
    setShopType(state, type) {
      state.shopType = type;
    },
    setCarrier(state, carrier) {
      state.carrier = carrier;
    },
    setUserAvatar(state, avatar) {
        state.user_avatar = avatar;
    },
    setCarrierState(state, carrier_state) {
        state.carrier_state = carrier_state;
    }
  },
});

export default store;
