import { createRouter, createWebHistory } from 'vue-router'
import VueAnalytics from 'vue-analytics'
import MemberPage from '@/views/MemberPage.vue'
import HomePage from '@/views/HomePage.vue'
import CouponPage from '@/views/CouponPage.vue'
import PointPage from '@/views/PointPage.vue'
import FormPage from '@/views/FormPage.vue'
import CardPage from '@/views/CardPage.vue'
import LotteryPage from '@/views/LotteryPage.vue'
import StoragePage from '@/views/StoragePage.vue'
import EventPage from '@/views/EventPage.vue'
import DepositPage from '@/views/DepositPage.vue'
import TicketPage from '@/views/TicketPage.vue'
import pageNotFound from '@/views/pageNotFound.vue'

const router = createRouter({
    mode: 'history',
    history: createWebHistory(process.env.BASE_URL),
    base: '/blankvue/',
    routes: [
        { path: '/', component: HomePage },
        { path: '/member', component: MemberPage },
        { path: '/coupon', component: CouponPage },
        { path: '/point', component: PointPage },
        { path: '/form', component: FormPage },
        { path: '/card', component: CardPage },
        { path: '/lottery', component: LotteryPage },
        { path: '/storage', component: StoragePage },
        { path: '/event', component: EventPage },
        { path: '/deposit', component: DepositPage },
        { path: '/ticket', component: TicketPage },
        { path: '/:catchAll(.*)', component: pageNotFound}
    ]
})

router.beforeEach((to, from, next) => {
    VueAnalytics.trackView(to.name);
    next();
});

export default router