<template>
  <div class="link_button button_notLogin" v-if="!loginState">
    <img :src="icon" class="link_button_icon" />
    <p>{{ title }}</p>
  </div>
  <RouterLink :to="routerPath" class="routerLink" v-else>
    <div class="link_button">
      <img :src="icon" class="link_button_icon" />
      <p>{{ title }}</p>
    </div>
  </RouterLink>
</template>
<script>
import RouterLink from 'vue-router';
export default {
    props: {
        title: {
            type: String,
            required: true,
        },
        icon: {
            type: String,
            default: "",
            required: true,
        },
        routerPath: {
            type: String,
            default: "",
            required: true,
        },
    },
    computed: {
        loginState() {
            return this.$store.state.isLogin;
        },
    },
    methods: {
        router(path) {
            this.$router.push(path);
        }
    },
    components: { RouterLink }
};
</script>
