  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  background-color: #eee;
  border-radius: 8px;
  padding: 10px 10px 0;

  img {
    width: 30px;
    height: 30px;
  }

  p {
    font-size: 12px;
    color: #999;
  }

}

.routerLink {
  text-decoration: none;
}

.button_notLogin {
  opacity: 0.6;
}
</style>
