<template>
  <div class="code_card" v-if="!loginState">
    <div class="buttons_container">
      <div v-for="(item, id) in tap_buttons" :key="id">
        <p>{{ item.name }}</p>
      </div>
    </div>
    <div class="to_login_hint">
      <p>請用 <span style="color: green">LINE</span> 登入</p>
    </div>
    <div class="to_login_hint">
      <p>免費領取VIP會員卡</p>
    </div>
  </div>
  <div class="code_card" v-else>
    <div class="buttons_container">
      <button
        v-for="(item, id) in tap_buttons"
        :key="id"
        :class="tabid == id ? 'tabed' : 'tab'"
        @click="changeTab(id)"
      >
        <p>{{ item.name }}</p>
      </button>
    </div>

    <QRCode :value="getQrcode" :size="100" level="H" v-if="tabid == 0" />
    
    <svg id="barcode" v-show="getCarrierState && tabid == 1"></svg>

    <div v-if="!getCarrierState && tabid == 1" class="bind_carrier_hint">
      <button @click="bind_carrier">綁定載具</button>
    </div>

    <div class="popup_cover" v-if="bind_carrier_popup"></div>
    <div class="popup_container" v-if="bind_carrier_popup">
      <h2>電子發票</h2>
      <input
        type="text"
        placeholder="請輸入載具編號"
        v-model="user_carrier"
        maxlength="8"
      />
      <div class="popup_button_container">
        <button @click="bind_cancel" class="cancel_button">取消</button>
        <button
          @click="bind_confirm"
          class="confirm_button"
          :disabled="user_carrier.length !== 8"
        >
          確認
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import JsBarcode from "jsbarcode";
import QRCode from "qrcode.vue";
export default {
  props: {},
  components: {
    QRCode,
  },
  data() {
    return {
      tabid: "0",
      tap_buttons: [
        {
          name: "會員QR碼",
          id: "0",
        },
        {
          name: "手機載具",
          id: "1",
        },
      ],
      bind_carrier_popup: false,
      user_carrier: "/",
    };
  },
  computed: {
    loginState() {
      return this.$store.state.isLogin;
    },
    getCarrier() {
      return this.$store.state.carrier;
    },
    getQrcode() {
      return this.$store.state.member_qrCode;
    },
    getCarrierState() {
      return this.$store.state.carrier_state;
    },
  },
  methods: {
    getBarcode() {
      new JsBarcode("#barcode", this.getCarrier, {
        format: "CODE128",
        displayValue: true,
        fontSize: 16,
        height: 60,
        width: 2,
        textMargin: 10,
        margin: 0,
        lineColor: "#000000",
      });
    },
    changeTab(id) {
      this.tabid = id;
      if (id == 1 && this.getCarrierState) {
        this.getBarcode();
      }
    },
    bind_carrier() {
      this.bind_carrier_popup = true;
    },
    bind_cancel() {
      this.bind_carrier_popup = false;
    },
    bind_confirm() {
      let user_carrier = this.user_carrier;
      user_carrier = user_carrier.toUpperCase();
      this.$store.commit("setCarrierState", true);
      this.$store.commit("setCarrier", user_carrier);
      this.user_carrier = "/";
      this.bind_carrier_popup = false;
      this.getBarcode();
    }
  },
};
</script>
