<template>
    <div class="body" v-if="!loginState">
      <login_card
        v-for="(item, index) in coffee_shops"
        :key="index"
        :title="item.name"
        :loginType="item.loginType"
        @login="login"
      ></login_card>
  
      <carrier_card></carrier_card>
  
      <div class="link_button_container">
        <link_button
          v-for="(item, index) in get_button_array"
          :key="index"
          :title="item.name"
          :icon="item.icon"
          :routerPath="item.routerPath"
        ></link_button>
      </div>
    </div>
    <div class="body" v-else>
      <vip_card :userName="getUserName" :loginType="getShopType" :avatar="getUserAvatar"></vip_card>
      <carrier_card></carrier_card>
      <div class="link_button_container">
        <link_button
          v-for="(item, index) in get_button_array"
          :key="index"
          :title="item.name"
          :icon="item.icon"
          :routerPath="item.routerPath"
        ></link_button>
      </div>
    </div>
  </template>
  
  <script>
  import axios from "axios";
  import login_card from "@/components/login_card.vue";
  import carrier_card from "@/components/carrier_card.vue";
  import link_button from "@/components/link_button.vue";
  import vip_card from "@/components/vip_card.vue";
  export default {
    components: {
      login_card,
      carrier_card,
      link_button,
      vip_card
    },
    beforeMount() {
      this.handleLoginCallback();
    },
    computed: {
      loginState() {
        return this.$store.state.isLogin;
      },
      getUserName() {
        return this.$store.state.userName;
      },
      getShopType() {
        return this.$store.state.shopType;
      },
      getUserAvatar() {
        return this.$store.state.user_avatar;
      },
      get_button_array() {
        const button_array = [
          {
            name: "會員",
            icon: require('@/assets/member.png'),
            routerPath: "/member",
          },
          {
            name: "優惠卷",
            icon: require('@/assets/coupon.png'),
            routerPath: "/coupon",
          },
          {
            name: "積點",
            icon: require('@/assets/point.png'),
            routerPath: "/point",
          },
          {
            name: "問卷",
            icon: require('@/assets/form.png'),
            routerPath: "/form",
          },
          {
            name: "集點卡",
            icon: require('@/assets/card.png'),
            routerPath: "/card",
          },
          {
            name: "抽獎",
            icon: require('@/assets/lottery.png'),
            routerPath: "/lottery",
          },
          {
            name: "寄存卡",
            icon: require('@/assets/storage.png'),
            routerPath: "/storage",
          },
          {
            name: "行銷活動",
            icon: require('@/assets/event.png'),
            routerPath: "/event",
          },
          {
            name: "儲值信託",
            icon: require('@/assets/deposit.png'),
            routerPath: "/deposit",
          },
          {
            name: "履約票卷",
            icon: require('@/assets/ticket.png'),
            routerPath: "/ticket",
          },
        ]
  
        return button_array;
      },
    },
    mounted() {
    },
    data() {
      return {
        coffee_shops: [
          {
            name: "Bryant咖啡館",
            loginType: "Bryant",
          },
          {
            name: "cama coffee",
            loginType: "cama",
          },
          {
            name: "咖啡深藏",
            loginType: "soul",
          },
        ],
      };
    },
    methods: {
      login(routerType) {
        let URL = "https://access.line.me/oauth2/v2.1/authorize?";
        URL += "response_type=code";
        URL += "&client_id=1661441879";
        URL += "&redirect_uri=https://qkqp3qr2.github.io/blankvue/";
        URL += `&state=${routerType}`;
        URL += "&scope=openid%20profile";
        window.location.href = URL;
        console.log("start login");
      },
      buildUrl(routerType) {
        let baseUrl = "https://access.line.me/oauth2/v2.1/authorize?";
        const params = {
          response_type: "code",
          client_id: "1661441879",
          redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
          state: "1",
          scope: "openid%20profile",
        };
  
        params.shop = routerType;
        const queryString = Object.entries(params)
          .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
          .join("&");
  
        return `${baseUrl}${queryString}`;
      },
      async handleLoginCallback() {
        const code = new URLSearchParams(window.location.search).get("code");
        if (code) {
          const accessToken = await this.getAccessToken(code);
          await this.getUserProfile(accessToken);
          await this.checkShopType();
          this.$store.commit("setLoginState");
          console.log("login successful");
        } else {
          console.error("Authorization code not found.");
        }
      },
      async getAccessToken(code) {
        try {
          const response = await axios.post(
            "https://api.line.me/oauth2/v2.1/token",
            new URLSearchParams({
              grant_type: "authorization_code",
              code,
              client_id: "1661441879",
              client_secret: "4f03d9065b14f7d6e394a0f20b0c53e3",
              redirect_uri: "https://qkqp3qr2.github.io/blankvue/",
            }).toString(),
            {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
              },
            }
          );
  
          return response.data.access_token;
        } catch (error) {
          console.error("Failed to get access token:", error);
          throw error;
        }
      },
      async getUserProfile(accessToken) {
        try {
          const response = await axios.get("https://api.line.me/v2/profile", {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
          });
  
          const userProfile = response.data;
          console.log(userProfile);
          this.$store.commit("setUserName", userProfile.displayName);
          this.$store.commit("setUserAvatar", userProfile.pictureUrl);
        } catch (error) {
          console.error("Failed to get user profile:", error);
        }
      },
      async checkShopType() {
        const shop = new URLSearchParams(window.location.search).get("state");
        if (shop) {
          this.$store.commit("setShopType", shop);
        } else {
          this.$store.commit("setShopType", "error");
        }
      },
    },
  };
  </script>
  
  <style lang="scss">
  html {
    padding: 0;
    margin: 0;
    overflow-x: hidden;
  }
  
  .body {
    width: 750rpx;
    height: 100%;
    overflow-y: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .link_button_container {
    width: 92vw;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
    padding: 10px 0;
  }
  </style>
  